import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Renderer(props = {}) {
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member } = UI.Section.Member.getProps(props)
  let state = UseReactive(reactive, { id: member.id, prop: 'type' })
  if (!state) { return null }
  const MemberType = Struct.string.capitalize(state.value)
  const Member = UI.use(`React.Section.${MemberType}`)
  return (<Member {...props} />)
}
