import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function MemberTree(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  const MemberPreviewer = UI.use('React.Section.Member.Previewer')
  const MemberActions = UI.use('React.View.Form.Builder.Actions')
  const MemberInserter = UI.use('React.View.Form.Builder.Inserter')
  let { reactive, member, onAction } = Helper.getProps(props)
  let parentId = reactive.family.ancestor(member).id
  let childre = UseReactive(reactive, { id: member.id, prop: 'children' }).value
  let type = reactive.get({ id: member.id, prop: 'type' })
  let typeDashed = Struct.string.dashCase(type)
  return (<>
    <div className={UI.joinClass({
      root: 'rcfm-form-builder',
      klass: ['member', typeDashed],
    })} >
      <MemberActions
        reactive={reactive}
        member={member}
        onAction={onAction}
      />
      <MemberPreviewer
        reactive={reactive}
        member={member}
        onAction={onAction}
      />
    </div>
    {childre.length ? <div className={UI.joinClass({
      root: 'rcfm-form-builder',
      klass: ['descendants', `descendants-of-${typeDashed}`],
    })} >
      {childre.map((chd) =>
        <MemberTree
          key={chd.id}
          reactive={reactive}
          member={chd}
          onAction={onAction}
        />,
      )}
    </div> : <MemberInserter
      reactive={reactive}
      member={member}
      onAction={onAction}
    />}
    <MemberInserter
      reactive={reactive}
      member={{ id: parentId }}
      onAction={(action = {}) => onAction({
        ...action, props: {
          ...action.props,
          order: reactive.get({ id: member.id, prop: 'order' }) + 1,
        },
      })}
    />
  </>)
}
