import { UI } from 'rcfm/helper'
import React from 'react'



export default function Highlight(props = {}) {
  const Helper = UI.Component.Highlight
  const Alert = UI.use('React.Component.Alert')
  const AlertText = UI.use('React.Component.AlertText')
  let { customProps, type, hint, on } = Helper.getProps(props)
  let className = Helper.joinWrapperClass(props)
  return (
    <div {...{ ...customProps, className }} >
      {props.children}
      {!on || !hint.length ? null :
        <Alert className="rcfm-component-highlight-hint" type={type} >
          {hint.map((h, i) => <AlertText key={i}>{h}</AlertText>)}
        </Alert>
      }
    </div>
  )
}
