import  { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React from 'react'



export default function Previewer(props = {}) {
  const Helper = UI.Section.Member
  const UseReactive = UI.React.hooks.useReactive
  let { reactive, member } = Helper.getProps(props)
  let state = UseReactive(reactive, { id: member.id, prop: 'type' })
  if (!state) { return null }
  let TypeCap = Struct.string.capitalize(state.value)
  const Previewer = UI.try([
    `React.Section.${TypeCap}.Previewer`,
    'React.Section.Member.Previewer.Fallback',
  ])
  return (<Previewer {...props} />)
}
