import { Struct } from 'rcfm'
import { UI } from 'rcfm/helper'
import React, { useState } from 'react'
import ChildSelect from './child-select.jsx'



export default function MemberInserter(props = {}) {
  const Helper = UI.Section.Member
  const Button = UI.use('React.Component.Button')
  let { reactive, member, onAction } = Helper.getProps(props)
  let [showSelect, setShowSelect] = useState(false)
  let childTypes = Helper.getChildTypes({ reactive, member })
  if (!childTypes.length) { return null }
  let type = reactive.get({ id: member.id, prop: 'type' })
  let typeDashed = Struct.string.dashCase(type)
  return (<div className={UI.joinClass({
    root: 'rcfm-form-builder',
    klass: ['inserter', `inserter-of-${typeDashed}`],
  })} >
    <Button
      className="rcfm-form-builder-inserter-trigger"
      circle color="dark" icon="plus"
      size={reactive.family.ancestor(member) ? 'small' : 'medium'}
      onClick={() => childTypes.length > 1 ? setShowSelect(true) : onAction({
        action: 'add', parentId: member.id, props: { type: childTypes[0].type },
      })}
    />
    {showSelect && <ChildSelect
      options={childTypes}
      onCancel={() => setShowSelect(false)}
      onSelect={(type) => {
        setShowSelect(false)
        onAction({
          action: 'add', parentId: member.id, props: { type },
        })}
      }
    />}
  </div>)
}
